.profile__name {
    max-width: 294px;
    font-family: 'Inter', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    color: #FFFFFF;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (min-width: 320px) and (max-width: 650px) {
    .profile__name {
        font-size: 27px;
        line-height: 33px;
        max-width: 189px;
        padding: 0;
    }
}
