.profile__about {
    max-width: 336px;
    font-family: 'Inter', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
    margin: 0;
    padding-top: 16px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (min-width: 320px) and (max-width: 650px) {
    .profile__about {
        max-width: 282px;
        font-size: 14px;
        line-height: 17px;
        padding-top: 14px;
        padding-right: 18px;
    }
}